import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Swal from 'sweetalert2'
import useJwt from '@src/auth/jwt/useJwt'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../ClientSideDataTable'
import { Link, useParams, useLocation } from 'react-router-dom'

const DetailsNotificationGroup = () => {
    const { id } = useParams()
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [groups, setGroups] =  useState([])
    const { search } = useLocation()
    const isPending = new URLSearchParams(search).get('is_pending')
    const user = JSON.parse(localStorage.getItem('userData'))
    useEffect(() => {
        const data = {
            group_id : parseInt(id),
            is_pending : isPending === 'true'
         }
        useJwt.detailsNotificationGroup(data).then(res => {
            console.log(res)
            setGroups(res.data.payload)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err.response)
            setTableDataLoading(false)
        })
    }, [resetData])

    const column = [
        {
            name: 'SL',
            minWidth: '20px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Group ID',
            minWidth: '100px',
            sortable: true,
            selector: 'group_id'
        },
        {
            name: 'MSISDN',
            minWidth: '100px',
            sortable: true,
            selector: 'msisdn'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'email'
        }
    ]

    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>Notification Groups Details</CardTitle>
                {
                    user.role === 'vendor' ?  <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allNotificationGroupsVendor' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple> :  <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allNotificationGroups' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
                }
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={groups} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    )
}

export default DetailsNotificationGroup